.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.product-categories {
  background: #f8f9fa;
}

.category-section {
  padding: 3rem 0;
  width: 85%;
  margin: 3em auto;
  border-radius: 50px;
}

.product-categories .category-section:nth-child(1),
.product-categories .category-section:nth-child(3) {
  background: #ffc107;
}

.product-categories .category-section:nth-child(even) {
  background: #8b4513;
}

.category-title {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  margin-bottom: 3rem;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.product-card {
  border-radius: 1em;
  text-align: center;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-card img {
  width: auto;
  height: 150px;
  object-fit: fill;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  padding: 1.5em;
}

.product-card h3 {
  font-weight: bold;
  margin: 3% 0;
}
.product-card h4 {
  font-weight: bold;
  margin: 3% 0;
}

.waffer-rolls-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 2rem 0;
}

.waffer-section {
  background: #ffc107;
  border-radius: 20px;
  padding: 1rem;
  text-align: center;
}

.rolls-section {
  background: #8b4513;
  border-radius: 20px;
  padding: 1rem;
  text-align: center;
}

.section-title {
  font-size: 2rem;
  font-weight: 900;
  color: white !important;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 2rem;
}

.waffer-section .product-grid,
.rolls-section .product-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  border-radius: 15px;
  padding: 1.5rem;
}

.waffer-section .product-card,
.rolls-section .product-card {
  background: white;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  width: 200px;
}

.waffer-section .product-card:hover,
.rolls-section .product-card:hover {
  transform: translateY(-5px);
}

.waffer-section .product-card img,
.rolls-section .product-card img {
  width: auto;
  height: 120px;
  object-fit: fill;
  border-radius: 8px;
  margin-bottom: 0.5rem;
}

.waffer-section .product-card h3,
.rolls-section .product-card h3 {
  font-size: 1rem;
  font-weight: bold;
}

.product-name {
  background-color: #fff;
  padding: 1px;
  border-radius: 0 0 1em 1em;
}

.waffer-section .product-card p,
.rolls-section .product-card p {
  font-size: 0.8rem;
  color: #8b4513;
  margin: 0;
}

.waffer-section .biscuit-name,
.rolls-section .biscuit-name {
  display: none;
}
.section-0 .waffer-name,
.section-1 .waffer-name,
.section-2 .waffer-name,
.section-3 .waffer-name,
.rolls-section .waffer-name {
  display: none;
}
.section-0 .roll-name,
.section-1 .roll-name,
.section-2 .roll-name,
.section-3 .roll-name,
.waffer-section .roll-name {
  display: none;
}

@media (max-width: 768px) {
  .waffer-rolls-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .waffer-section .product-grid,
  .rolls-section .product-grid {
    grid-template-columns: 1fr;
  }

  .category-title {
    font-size: 2rem;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  .template-product-image {
    width: 250px;
  }

  .category-section {
    width: 100%;
  }
  .waffer-rolls .container {
    width: 100%;
    padding: 0;
  }
  .waffer-section .product-card,
  .rolls-section .product-card {
    width: auto;
  }
  .waffer-rolls {
    padding: 0;
  }
}
