.ant-modal-content {
  padding: 10px !important;
}
.product-container {
  text-align: center;
  background-color: #fff;
}

.product-logo {
  width: auto;
  margin: 0 auto 1.5em;
}

.template-product-card {
  border-radius: 20px;
  padding: 2em 1em;
  max-width: 80%;
  margin: 0 auto;
}

.product-title {
  background: #fff;
  padding: 0.5em 2em;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.3em;
}

.product-content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 4em;
}

.template-product-image img {
  width: auto;
  border-radius: 16px;
  background: #fff;
  padding: 5em 3em;
}

.product-table {
  border-collapse: collapse;
  width: auto;
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
}

.product-table th,
.product-table td {
  padding: 0.8em;
  font-size: 1.2em;
}

.product-table th {
  color: #fff;
}

.product-description {
  max-width: 50%;
  font-size: 1.4em;
  line-height: 1.6em;
  margin: 2em auto;
}

.contact-strip {
  border-top-left-radius: 100% 60px;
  border-top-right-radius: 100% 60px;
  margin-top: 10em;
}

.curve-bg {
  padding-top: 3em;
  padding-bottom: 3em;
  position: relative;
}

.biscuit-img {
  width: auto;
  position: absolute;
  margin: 0 auto;
  transform: translateX(-50%);
  z-index: 2;
  top: -50%;
}

.contact-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 5em;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: white;
  font-size: 18px;
  font-weight: 500;
}

.contact-item .icon {
  font-size: 1rem;
}

.email {
  width: 100%;
  justify-content: center;
  margin-top: 1em;
}
.curve-bg-container {
  position: relative;
  height: 5%;
  overflow: hidden;
}

.curve-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.ant-modal {
  width: auto !important;
}
.ant-modal-footer {
  display: none;
}
.biscuit-description {
  display: block;
}

.curve-svg-mobile {
  display: none;
}
@media screen and (max-width: 768px) {
  .product-content {
    flex-direction: column;
    align-items: center;
  }

  .product-image img {
    width: 100%;
  }

  .product-table {
    width: 100%;
  }
  .product-title {
    font-size: 1.2em;
    padding: 0.2em 1em;
    width: 90%;
  }
  .product-description {
    max-width: 90%;
  }
  .biscuit-img {
    width: 50%;
  }
  .template-product-image img {
    width: 65%;
  }
  .biscuit-img {
    top: 0;
  }
  .curve-svg {
    display: none;
  }
  .curve-svg-mobile {
    display: initial;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    border-radius: 20px;
    margin: 2em 0;
  }
  .contact-strip {
    margin-top: 0;
    padding: 0;
  }
  .template-product-image {
    width: 240px;
  }
  .contact-item {
    margin: 0;
  }
  .curve-bg {
    margin-bottom: 0;
  }
  .contact-details {
    gap: 1em;
  }
  .product-table th,
  .product-table td {
    padding: 0.5em;
  }
}
