.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.about-section {
  background: white;
  padding: 3rem 0;
}

.brand-description {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #333;
  text-align: center;
  max-width: 1000px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .brand-description {
    font-size: 1rem;
    padding: 0 1rem;
  }
}
