.footer-section {
  position: relative;
  background: url("../../assets/images/Bear-FooterBG.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 35em;
  padding-bottom: 5em;
}

.footer-content {
  align-items: center;
  gap: 2rem;
}

.contact-info {
  margin-left: 50%;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  line-height: 1.5;
  margin-bottom: 15px;
}

.contact-text {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  color: #6a3e20;
  font-weight: 500;
  gap: 8px;
}

/* Responsive */
@media (max-width: 900px) {
  .footer-content {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .contact-info {
    align-items: center;
  }
  .contact-info {
    margin-left: 0;
  }
  .contact-text {
    font-size: 1rem;
    color: #000;
    text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.5);
  }
  .category-section {
    margin: 5px 0;
  }
  .footer-section {
    background-size: contain;
    padding-top: 1em;
    padding-bottom: 0;
    background-position: bottom;
  }
}
